import React from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Link } from 'react-router-dom';
import styles from './reset-password.module.css';
import { PasswordInput, Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useDispatch } from "react-redux";
import { saveNewPassword } from "../../services/actions/userInfo";

function ResetPassword() {

  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [password, setPassword] = React.useState('');
  const [code, setCode] = React.useState('');

  const checkHasAccess = React.useCallback(() => {
    return location.state && location.state.hasAccess !== null ? location.state.hasAccess : false;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[]);

  function onSubmit(e) {
    e.preventDefault();
    code && password && dispatch(saveNewPassword(password, code))
      .then(() => {
        navigate('/login');
      });
  }

  return (
    <main className={styles.main}>

      {/* Disable direct route */}
      {
        checkHasAccess() === false ? (
          <p className="text text_type_main-medium">
            {'  ...   '}
          </p>
        ) : (
          <>
            <form className={`${styles.form} mb-20`} onSubmit={onSubmit}>
              <h1 className="text text_type_main-medium"> </h1>
              <PasswordInput
                onChange={(e) => setPassword(e.target.value)}
                placeholder="  "
                value={password}
                name={'password'}
                errorText={'    6 '}
              />
              <Input
                type={'text'}
                placeholder={'   '}
                onChange={(e) => setCode(e.target.value)}
                value={code}
                name={'code'}
              />
              <Button
                extraClass={styles.Button}
                htmlType="submit"
                type="primary"
                size="medium"
              ></Button>
            </form>
            <p className="text text_type_main-default">
               ?
              <Link className={`${styles.link} pl-2`} to='/login'></Link>
            </p>
          </>
        )
      }
    </main>

  );
}

export default ResetPassword;
