import React from "react";
import { useNavigate } from "react-router-dom";
import { Link } from 'react-router-dom';
import styles from './forgot-password.module.css';
import { EmailInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { resetPassword } from "../../services/actions/userInfo";
import { useDispatch, useSelector } from "react-redux";

function ForgotPassword() {

  const dispatch = useDispatch();

  const [email, setEmail] = React.useState('');
  const navigate = useNavigate();

  const hasAccessToResetPassword = useSelector((store) => store.userInfo.email !== null);

  React.useEffect(() => {
    hasAccessToResetPassword && navigate('/reset-password', {
      replace: true,
      state: { hasAccess: true }
    });
  }, [hasAccessToResetPassword, navigate]);


  function onSubmit(e) {
    e.preventDefault();
    email && dispatch(resetPassword(email));
  }

  return (
    <main className={styles.main}>
      <form className={`${styles.form} mb-20`} onSubmit={onSubmit}>
        <h1 className="text text_type_main-medium"> </h1>
        <EmailInput
          onChange={(e) => setEmail(e.target.value)}
          placeholder=" e-mail"
          value={email}
          name={'email'}
          isIcon={false}
          errorText={'    e-mail'}
        />
        <Button
          extraClass={styles.Button}
          htmlType="submit"
          type="primary"
          size="medium"
        ></Button>
      </form>
      <p className="text text_type_main-default">
         ?
        <Link className={`${styles.link} pl-2`} to='/login'></Link>
      </p>
    </main>

  );
}

export default ForgotPassword;
