import { useSelector } from "react-redux";
import orderDetailsStyles from './order-details.module.css';
import imgDone from '../../images/done.svg';

function OrderDetails() {

  const { numOfOrder } = useSelector((store) => store.order);

  return (
    <section className={`${orderDetailsStyles.section} pt-4 pb-15`} aria-label='  '>
      <p className={`${orderDetailsStyles.id} text text_type_digits-large`}>{numOfOrder}</p>
      <p className={`${orderDetailsStyles.description} text text_type_main-medium pt-8`}> </p>
      <img className={`${orderDetailsStyles.image} pt-15 pb-15`} src={imgDone} alt="" />
      <p className={`${orderDetailsStyles.status} text text_type_main-default`}>   </p>
      <p className={`${orderDetailsStyles.waitingArea} text text_type_main-default text_color_inactive pt-2`}>    </p>
    </section>
  );
}

export default OrderDetails;
