import React from "react";
import { Link } from 'react-router-dom';
import styles from './register.module.css';
import { EmailInput, PasswordInput, Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useDispatch } from "react-redux";
import { registerUser } from "../../services/actions/userInfo";

function Register() {

  const dispatch = useDispatch();

  const [firstname, setFirstname] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');

  function onSubmit(e) {
    e.preventDefault();
    firstname && email && password && dispatch(registerUser(email, password, firstname));
  }

  return (
    <main className={styles.main}>
      <form className={`${styles.form} mb-20`} onSubmit={onSubmit}>
        <h1 className="text text_type_main-medium"></h1>
        <Input
          type={'text'}
          placeholder={''}
          onChange={(e) => setFirstname(e.target.value)}
          value={firstname}
          name={'name'}
        />
        <EmailInput
          onChange={(e) => setEmail(e.target.value)}
          value={email}
          name={'email'}
          isIcon={false}
          errorText={'    e-mail'}
        />
        <PasswordInput
          onChange={(e) => setPassword(e.target.value)}
          value={password}
          name={'password'}
          errorText={'    6 '}
        />
        <Button
          extraClass={styles.Button}
          htmlType="submit"
          type="primary"
          size="medium"
        ></Button>
      </form>
      <p className="text text_type_main-default">
         ?
        <Link className={`${styles.link} pl-2`} to='/login'></Link>
      </p>
    </main>

  );
}

export default Register;
